<template>
  <div ref="rootElement" class="rnd-container" 
    :class="{ 'rnd-state-open': isShowSuggest || isShowLoading }">
    <div class="rnd-input-container">
      <div class="rnd-flex">
        <div class="rnd-text">
          <input ref="inputElement" type="text" 
            @input="input()"
            @focus="focus()"
            @click="click()"
            @blur="blur()"
            @keyup.up="key('up')"
            @keyup.down="key('down')"
            @keyup.enter="key('enter')"
            @keyup.esc="key('esc')"
            @keypress="canEnter = true"/>
        </div>
        <button class="rnd-button" @blur="blur()" tabindex="-1">
          <slot name="add-on-icon">
            <svg class="svg-icon" viewBox="0 0 20 20">
							<path fill="#346DF0" d="M12.323,2.398c-0.741-0.312-1.523-0.472-2.319-0.472c-2.394,0-4.544,1.423-5.476,3.625C3.907,7.013,3.896,8.629,4.49,10.102c0.528,1.304,1.494,2.333,2.72,2.99L5.467,17.33c-0.113,0.273,0.018,0.59,0.292,0.703c0.068,0.027,0.137,0.041,0.206,0.041c0.211,0,0.412-0.127,0.498-0.334l1.74-4.23c0.583,0.186,1.18,0.309,1.795,0.309c2.394,0,4.544-1.424,5.478-3.629C16.755,7.173,15.342,3.68,12.323,2.398z M14.488,9.77c-0.769,1.807-2.529,2.975-4.49,2.975c-0.651,0-1.291-0.131-1.897-0.387c-0.002-0.004-0.002-0.004-0.002-0.004c-0.003,0-0.003,0-0.003,0s0,0,0,0c-1.195-0.508-2.121-1.452-2.607-2.656c-0.489-1.205-0.477-2.53,0.03-3.727c0.764-1.805,2.525-2.969,4.487-2.969c0.651,0,1.292,0.129,1.898,0.386C14.374,4.438,15.533,7.3,14.488,9.77z"></path>
						</svg>
          </slot>
        </button>
      </div>
    </div>
    <div class="rnd-table-container" v-if="isShowSuggest || isShowLoading">
      <div class="rnd-table">
        <div class="rnd-table-t-border"/>
        <ul v-if="!isShowLoading">
          <li v-for="(item, index) in suggestItems" 
            :key="index"
            :class="{current: item.current}"
            @mouseover="mouseover(item)"
            @mouseout="mouseout(item)">
            <div class="rnd-item-container" @click="itemSelect(item)">
              <slot :text="item.text" :current="item.current">
                <div class="rnd-item-body">{{item.text}}</div>
              </slot>
            </div>
          </li>
        </ul>
        <div v-if="isShowLoading" class="rnd-item-loading">
          <slot name="loading">読み込み中...</slot>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    value: {
      default: null
    },
    items: {
      type: [Array, Promise, Function],
      default: null
    },
    maxSuggest: {
      type: Number,
      default: 10
    },
    filterFunc: {
      type: [Function],
      default: null
    },
    queryInterval: {
      type: Number,
      default: 100
    },
    loadingShowDelay: {
      type: Number,
      default: 500
    },
  },
  watch: {
    value: function(newVal) {
      if (this.isMounted && this.$refs.inputElement.value != newVal) {
        this.$refs.inputElement.value = newVal
      }
    }
  },
  data() {
    return {
      lastSearchTime: 0,
      searchQueueCount: 0,
      isShowSuggest: false,
      suggestItems: [],
      focusSuppress: false,
      isShowLoading: false,
      isMounted: false,
      canEnter: false,
    }
  },
  computed: {
    currentIndex: function() {
      let currentIndex = -1
      this.suggestItems.forEach((item,index) => {
        if (item.current) currentIndex = index
      });
      return currentIndex
    },
  },
  mounted() {
    this.isMounted = true
  },
  methods: {
    input() {
      this.openSuggest()
      this.onInput()
    },
    focus() {
      //console.log("fff")
      !this.focusSuppress && this.openSuggest()
      this.focusSuppress = false
    },
    click() {
      if (this.searchQueueCount == 0 && !this.isShowSuggest) {
        this.openSuggest()
      }
    },
    blur() {
      // console.log("blue")
      // let elList = document.querySelectorAll(".rnd-container:hover");
      // for (let i = 0; i < elList.length; i++) {
      //   if (elList[i] == this.$refs.rootElement) {
      //     return
      //   }
      // }
      // this.closeSuggest()
      setTimeout(() => this.closeSuggest(), 100)
    },
    key(keyName) {

      if (keyName == "enter") {
        if (this.canEnter && this.isShowSuggest && this.currentIndex >= 0) { 
          this.setCurrentText(true)
          this.closeSuggest()
        }
      } else if (keyName == "up") {
        this.moveCurrentSuggest(-1)
      } else if (keyName == "down") {
        this.moveCurrentSuggest(1)
      } else if (keyName == "esc") {
        this.closeSuggest()
      }
      this.canEnter = false
    },
    mouseover(item) {
      this.suggestItems.forEach(n => n.current = false)
      item.current = true
    },
    mouseout(item) {
      item.current = false
    },
    async openSuggest() {
      try {
        let listItems = await this.queryItems()
        if (listItems == null) return

        this.executeFilter(listItems, this.$refs.inputElement.value)
        this.suggestItems.forEach(n => n.current = false)

        if (document.activeElement != this.$refs.inputElement) {
          this.isShowSuggest = false
        } else if (this.suggestItems.length >= 2) {
          this.isShowSuggest = true
        } else if(this.suggestItems.length == 1 
            && this.suggestItems[0].text != this.$refs.inputElement.value) {
          this.isShowSuggest = true
        } else {
          this.isShowSuggest = false
        }
      } finally {
        //
      }
    },
    closeSuggest() {
      this.isShowSuggest = false
    },
    async queryItems() {
      try {
        if (this.searchQueueCount >= 2) return null
        this.searchQueueCount++

        let flg = this.searchQueueCount == 1
        if (!flg) await this.wait(this.queryInterval)

        let result;
        if (typeof this.items === "function") {
          result = await this.executeFuncQuery()
        } else {
          result = this.items
        }

        if (flg) {
          let w = this.wait(this.queryInterval)
          w.then(() => this.searchQueueCount--)
        } else {
          this.searchQueueCount--
        } 
        return result
      } finally {
        //console.log("searchQueueCount=" + this.searchQueueCount)
      }
    },
    async executeFuncQuery() {
      let result = this.items(this.$refs.inputElement.value)

      if (this.isPromise(result)) {
        let executingPromiss = true
        setTimeout(() => {
          this.isShowLoading = executingPromiss
        }, this.loadingShowDelay)

        let data = await result
        this.isShowLoading = executingPromiss = false
        return data
      } else {
        return result instanceof Array ? result : []
      }
    },
    executeFilter(listItems, inputValue) {
      this.suggestItems.length = 0
      for (let i = 0; i < listItems.length; i++) {
        if (this.filterItem(listItems[i], inputValue) === true) {
          this.suggestItems.push({
            text: listItems[i],
            current: false
          })
        }
        if (this.suggestItems.length >= this.maxSuggest) break
      }
    },
    filterItem(item, inputValue) {
      if (typeof this.filterFunc === "function") {
        return this.filterFunc(item, inputValue)
      } else if (typeof this.items === "function") {
        return true
      } else {
        if (inputValue && inputValue.trim().length === 0) return true
        return item.toUpperCase().indexOf(inputValue.toUpperCase()) >= 0
      }
    },
    isPromise(obj) {
      return obj instanceof Promise || (obj && typeof obj.then === 'function')
    },
    itemSelect(item) {
      this.$refs.inputElement.value = item.text
      this.closeSuggest()
      this.focusSuppress = true
      setTimeout(() => { 
        this.$refs.inputElement.focus()
        this.onInput()
        this.onSearch()
      }, 0)
    },
    moveCurrentSuggest(move) {
      if (this.suggestItems.length <= 0) return
      let currentIndex = this.currentIndex
      
      currentIndex += move
      if (currentIndex < 0) currentIndex = this.suggestItems.length -1
      if (currentIndex >= this.suggestItems.length) currentIndex = 0
      this.suggestItems.forEach((item,index) => {
        item.current = (index == currentIndex)
      });
      this.setCurrentText()
    },
    setCurrentText(raiseEvent) {
      if (this.currentIndex >= 0) {

        this.$refs.inputElement.value = this.suggestItems[this.currentIndex].text 
        var pos = this.$refs.inputElement.value.length
        setTimeout(() => {
          this.$refs.inputElement.setSelectionRange(pos, pos)
          this.onInput()
          if (raiseEvent === true) {
            this.onSearch()
          }
        }, 1);
      }
    },
    onSearch() {
      this.$emit('search');
    },
    onInput() {
      this.$emit('input', this.$refs.inputElement.value) 
    },
    wait(interval) {
      return new Promise((resolve) => {
        setTimeout(() => {
          resolve()
        }, interval);
      })
    },
  },
}
</script>

